<template>
  <form>
      <h3 class="text-muted my-4">添加新的商品：</h3>
      <div class="row form-group">
          <label class="col-sm-1">品种</label>
          <div class="col-sm-11">
              <input type="text" class="form-control" v-model="newProduct.name">
          </div>
      </div>
      <div class="row form-group">
          <label class="col-sm-1">描述</label>
          <div class="col-sm-11">
              <textarea rows="5" class="form-control" v-model="newProduct.description"></textarea>
          </div>
      </div>
      <p><strong>选项1:</strong></p>
      <div class="row form-group">
          <label class="col-sm-1">尺寸</label>
          <div class="col-sm-11">
              <input type="text" class="form-control" v-model="newProduct.size1">
          </div>
      </div>
      <div class="row form-group">
          <label class="col-sm-1">价格</label>
          <div class="col-sm-11">
              <input type="text" class="form-control" v-model="newProduct.price1">
          </div>
      </div>
      <p><strong>选项2:</strong></p>
      <div class="row form-group">
          <label class="col-sm-1">尺寸</label>
          <div class="col-sm-11">
              <input type="text" class="form-control" v-model="newProduct.size2">
          </div>
      </div>
      <div class="row form-group">
          <label class="col-sm-1">价格</label>
          <div class="col-sm-11">
              <input type="text" class="form-control" v-model="newProduct.price2">
          </div>
      </div>
      <div class="form-group row">
        <button @click="addMenuItem" type="button" class="btn btn-success btn-block">添加</button>
      </div>
      
  </form>
</template>
<script>
// import axios from 'axios'
export default {
  data(){
      return{
          newProduct:{}
      }
  },
  methods:{
      addMenuItem(){
          let data = {
              name:this.newProduct.name,
              description:this.newProduct.description,
              options:[
                  {
                      size:this.newProduct.size1,
                      price:this.newProduct.price1
                  },
                  {
                      size:this.newProduct.size2,
                      price:this.newProduct.price2
                  }
              ]
          }
          // axios,vue-resource（官方已停止更新）,es6 fetch
          // es6 fetch
        //   fetch("https://wd5524282109kqties.wilddogio.com/goods.json",{
        //       method:"POST",
        //       headers:{
        //           "Content-type":"application/json"
        //       },
        //       body:JSON.stringify(data)
        //   })
        //     .then(res =>{
        //         res.json()
        //     })
        //     .then(data => this.$router.push({name:"menuLink"}))
        //     .catch(err => alert(err))

        // axios
        this.http.post("/goods.json",data)
            // .then(res => console.log(res))
            // .then(res => this.$router.push({name:"menuLink"}))
            // 数据同步到vuex中
            .then(res => this.$store.commit('pushToMenuItems',data))
            .catch(err => alert("出现错误" + err))
      }
  }
}
</script>



