<template>
    <div class="row">
        <div id="home" class="col-sm-12 text-center">
            <div class="background">
                <h1>这里是首页第一行</h1>
                <h2>这里是首页第二行</h2>
                <button @click="goToMenu" class="btn btn-success">Let's order!</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  methods:{
      goToMenu(){
        // 跳转到上一步浏览的页面
        //   this.$router.go(-1)

        // 指定跳转地址
        // this.$router.replace('/menu')

        // 跳转到指定名字的路由下
        // this.$router.replace({name:'menuLink'})

        // 通过push来进行跳转
        // this.$router.push('/menu')
        this.$router.push({name:'menuLink'})
      }
  }
}
</script>

<style>
    #home{
        background: url('../../src/assets/1.jpg');
        height: 85vh;
        padding: 10%;
    }

    h1,h2{
        margin: 6%;
    }

    .background{
        background: #eee;
        opacity: 0.8;
        max-width: 75vw;
        margin: 0 auto;
        padding: 20px 0;
    }
</style>





