<template>
    <div class="row">
        <div class="col-sm-12 col-md-8">
            <app-new-goods></app-new-goods>
        </div>
        <div class="col-sm-12 col-md-4">
            <h3 class="text-muted my-4">Menu:</h3>
            <table class="table">
                <thead class="table table-default">
                    <tr>
                        <th>商品</th>
                        <th>删除</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems" :key="item.name">
                    <tr>
                        <td>{{ item.name }}</td>
                        <td>
                            <button @click="deleteProduct(item)" class="btn btn-sm btn-outline-danger">&times;</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import NewGoods from './NewGoods'
export default {
    components:{
        "app-new-goods":NewGoods
    },
    data(){
        return{
            // getMenuItems:[]
        }
    },
    computed:{
        getMenuItems:{
            get(){
                //获取vuex中的数据
                // return this.$store.state.menuItems
                // 通过getters获取数据
                return this.$store.getters.getMenuItems
            },
            set(){

            }
        }
    },
    created(){
        fetch("https://wd5524282109kqties.wilddogio.com/goods.json")
            .then(res => {
                return res.json()
            })
            .then(data => {
                // console.log(data)
                let menuArray = []
                for(let key in data){
                    // console.log(key)
                    // console.log(data[key])
                    data[key].id = key
                    menuArray.push(data[key])
                }
                // this.getMenuItems = menuArray
                // 同步到vuex
                this.$store.commit('setMenuItems',menuArray)
            })
    },
    methods:{
        deleteProduct(item){
            // 方法1：使用fetch
            fetch("https://wd5524282109kqties.wilddogio.com/goods/"+item.id+"/.json",{
                method:"DELETE",
                headers:{
                    "Content-type":"application/json"
                }
            })
                .then(res => res.json())
                .then(data => {
                    console.log(item.id)
                    // this.getMenuItems.splice(this.getMenuItems.indexOf(item),1)
                    this.$store.commit('removeMenuItem',item)
                })
                .catch(err => alert(err))

            // 方法2：使用axios
            

        }
    }
}
</script>




