import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import { routes } from './routes'
import axios from 'axios'

import {store} from './store/store.js'

Vue.use(VueRouter)

//axios 基本路径，《野狗》实时通信引擎
axios.defaults.baseURL = 'https://wd5524282109kqties.wilddogio.com/'

//设置Vue原型，在任何页面不导入axios的情况下均可使用axios。this.http.get()或者post即可。
Vue.prototype.http = axios

const router = new VueRouter({
  routes,
  // mode:'history',
  scrollBehavior(to,from,savedPosition){
    // return{x:0,y:100}
    // return{ selector:'.btn'}

    if(savedPosition){
      return savedPosition
    }else{
      return{ x:0, y:0}
    }
  }
})

// 全局守卫
