//   // alert("你还没有登录，请登录！");
//   // next();

//   判断store.getters.isLogin === true
//   if(to.path == '/login' || to.path == '/register' || to.path.indexOf('about')>0){
//     next();
//   }else{
//     alert("你还没有登录，请登录！");
//     next('/login');
//   }
// })

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



