import Home from './components/Home'
import Menu from './components/Menu'
import Admin from './components/Admin'
import Login from './components/Login'
import Register from './components/Register'
import About from './components/about/About'

// 二级路由
import Contact from './components/about/Contact'
import History from './components/about/History'
import OrderingGuide from './components/about/OrderingGuide'
import Delivery from './components/about/Delivery'

// 三级路由
import Phone from './components/about/contact/Phone'
import PersonName from './components/about/contact/PersonName'

export const routes = [
    {path:'/',name:'homeLink',components:{
        default:Home,
        'orderingGuide':OrderingGuide,
        'history':History,
        'delivery':Delivery
    }},
    {path:'/menu',name:'menuLink',component:Menu},
    {path:'/admin',name:'adminLink',component:Admin
      // ,beforeEnter:(to,from,next) =>{
      //   // beforeEnter:路由独享守卫
      //   alert("你还没有登录，请先登录！");
      //   next('/login');
      // }
    },
    {path:'/about',name:'aboutLink',redirect:'/about/history',component:About,children:[
      {path:'/about/contact',name:'contactLink',redirect:'/about/contact/phone',component:Contact,children:[
        {path:'/about/contact/phone',name:'phoneLink',component:Phone},
        {path:'/about/contact/name',name:'personNameLink',component:PersonName}
      ]},
      {path:'/about/history',name:'historyLink',component:History},
      {path:'/about/delivery',name:'deliveryLink',component:Delivery},
      {path:'/about/orderingGuide',name:'orderingGuideLink',component:OrderingGuide}
    ]},
    {path:'/login',name:'loginLink',component:Login},
    {path:'/register',name:'registerLink',component:Register},
    {path:'*',redirect:'/'}
  ]


