<template>
    <div class="row mt-3">
        <!-- 菜单 -->
        <div class="col-sm-12 col-md-8" style="background:#f8f9fa;">
            <table class="table">
                <thead class="thead-default">
                    <tr>
                        <th>尺寸</th>
                        <th>价格</th>
                        <th>加入</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems" :key="item.name">
                    <tr>
                        <td><strong>{{item.name}}</strong><br><span style="color:#ccc;font-size:12px;">{{item.description}}</span></td>
                    </tr>
                    <tr v-for="option in item.options" :key="option.size">
                        <td>{{ option.size }}</td>
                        <td>{{ option.price }} 元</td>
                        <td>
                            <button @click="addToBasket(item,option)" class="btn btn-sm btn-outline-success">+</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!-- 购物车 -->
        <div class="col-sm-12 col-md-4">
            <div v-if="baskets.length > 0">
                <table class="table">
                    <thead class="thead-default">
                        <tr>
                            <th>数量</th>
                            <th>名称</th>
                            <th>价格</th>
                        </tr>
                    </thead>
                    <tbody v-for="item in baskets" :key="item.name">
                        <tr>
                            <td>
                                <button @click="decreaseQuantity(item)" class="btn btn-sm">-</button>
                                <span>{{ item.quantity }}</span>
                                <button @click="increaseQuantity(item)" class="btn btn-sm">+</button>
                            </td>
                            <td>{{item.name}} {{item.size}}</td>
                            <td>{{item.price * item.quantity}}</td>
                        </tr>
                    </tbody>
                </table>
                <p>总价：{{total + "元"}}</p>
                <button class="btn btn-success btn-block">结算</button>
            </div>
            <div v-else>
                {{ basketText }}
            </div>
        </div>
    </div>
</template>
<script>
// import axios from 'axios'  //在哪main.js里设置axios的Vue原型
export default {
  data(){
      return{
          baskets:[],
          basketText:"购物车没有任何商品",
        //   getMenuItems:{}
      }
  },
  computed:{
      getMenuItems(){
          //获取vuex中的数据
        //   return this.$store.state.menuItems
        // 通过getters获取数据
        return this.$store.getters.getMenuItems
      },
      total(){
          let totalCost = 0
          for(let index in this.baskets){
              let iItem = this.baskets[index]
              totalCost += iItem.quantity * iItem.price
          }
          return totalCost
      }
  },
  created(){
      this.fetchData()
  },
  methods:{
      fetchData(){
        // fetch("https://wd5524282109kqties.wilddogio.com/goods.json")
        //     .then(res => {
        //         return res.json()
        //     })
        //     .then(data => {
        //         this.getMenuItems = data
        //     })
        //     .catch(err => alert(err))

        // axios 
        // axios.get("goods.json")
        //     .then(res => {
        //         this.getMenuItems = res.data
        //     })

        // this.http.get("goods.json")
        //     .then(res => {
        //         this.getMenuItems = res.data
        //     })

        // 将请求下来的数据保存到vuex中
        this.http.get("goods.json")
            .then(res => this.$store.commit("setMenuItems",res.data))
      },
      addToBasket(item,option){
          let basket = {
              name:item.name,
              size:option.size,
              price:option.price,
              quantity:1
          }
          if(this.baskets.length > 0){
              // 过滤
              let result = this.baskets.filter((basket) => {
                  return (basket.name === item.name && basket.price === option.price)
              })

              if(result != null && result.length > 0){
                  result[0].quantity++
              }else{
                  this.baskets.push(basket)
              }
          }else{
              this.baskets.push(basket)
          }          
      },
      decreaseQuantity(item){
          item.quantity--
          if(item.quantity <= 0){
              this.removeFromBasket(item)
          }
      },
      increaseQuantity(item){
          item.quantity++
      },
      removeFromBasket(item){
          this.baskets.splice(this.baskets.indexOf(item),1)
      }
  }
}
</script>



